import { call, put, takeEvery } from "redux-saga/effects";
import axios from "axios";
import { success, failed } from "./lyrics";

const GET_ASYNCDATA = "TEST"; // 이 액션으로 체킹해서 호출

export const onGetAsyncData = () => ({ type: GET_ASYNCDATA });

// dispatch => checking =>
function* getAsyncData() {
  try {
    const data = yield call(axios.get("http://localhost:3001/api/insert"));
    console.log(data);
    yield put(success);
  } catch (error) {
    yield put(failed);
  }
}

function* rootSaga() {
  yield takeEvery(GET_ASYNCDATA, getAsyncData);
}

export default rootSaga;
